html {
	background: #7C6883;
	padding-bottom: 5em;
}

body {
	margin: 3em auto;
	padding: 10%;
	width: 50%;
	background: #514B79;
	font-family: sans-serif;
	box-shadow: inset 0 2px 5px #22244A;
	border-radius: 4em;
	text-align: center;
	font-size: 1.5em;
	line-height: 1.4;
}

h1,h2 {
	color: #BE828A;
	text-shadow: 0 2px 5px #22244A;
	margin-top: 0;
}

h2 {
	text-shadow: 0 1px 2px #22244A;
}

a {
	color: #EEC58D;
	text-decoration: none;

	&:hover {
		text-decoration: underline;
	}
}

section {
	margin-top: 8em;
	background: #7C6883;
	padding: 1em 2em;
	border-radius: 2em;
	box-shadow: 0 2px 5px #22244A;

	a {
		position: relative;

		text-shadow: 0 1px 2px #22244A;

		&:hover {
			text-shadow: 0 0px 1px #22244A;
			top: 1px;
		}
	}
}




@media all and (max-width: 480px) {
	body {
		margin: 1em;
		font-size: 1.4em;
		width: auto;
		line-height: 1.3;
	}

	h1 {
		font-size: 1.5em;
	}
}